  .event-details {
    position: fixed;
    top: 0;
    right: -100%; /* Initially off-screen */
    width: 100%;
    height: 100%;
    background-color: white;
    transition: right 0.3s ease; /* Add a smooth transition */
  }
  
  .event-details.slide-left {
    right: 0; /* Slide in to the screen */
  }
  
